import React, { useEffect, useRef, useState } from 'react'

import "../css/CustomButtons.css"
import "../css/MainBackground.css"

import bgImage from "../img/bg/heroheader-bg.png"
import banner from "../img/banner/banner.png"
import { Link } from 'react-router-dom'

export default function RandomWinner(props) {
    const { account, gameContract, connected, ConnectAccount } = props;

    const totalPlayers = useRef(null);
    const totalWinnersYouWant = useRef(null);

    const [mainLoading, setMainLoading] = useState(true);
    const [currentRoundForWinners, setCurrentRoundForWinners] = useState(0);

    const [loadingRandomWinners, setLoadingRandomWinners] = useState(false);
    const [loadedRandomWinners, setLoadedRandomWinners] = useState(null);

    const [findingRandomWinnersNotice, setFindingRandomWinnersNotice] = useState(null);


    const [isAdmin, setisAdmin] = useState(false);


    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, []);

    useEffect(() => {
        if (connected) {
            loadData();
        }
    }, [gameContract]);




    const connectToAccount = () => {
        if (!connected) {
            ConnectAccount()
        }
    }

    const loadData = async () => {
        setMainLoading(true);
        const getIsAdmin = await gameContract.methods.isAdmin().call();
        setisAdmin(getIsAdmin);
        if (!getIsAdmin) {
            setFindingRandomWinnersNotice('Sorry! Only Admin can Generate Random Number')
        }
        const currentRoundForWinners = await gameContract.methods.currentRoundForWinners().call();
        setCurrentRoundForWinners(currentRoundForWinners.toNumber());

        setMainLoading(false);
    }


    const findRandomWinners = async () => {

        if (connected && gameContract != null) {

            var _totalPlayers = totalPlayers.current.value;
            var _totalWinnersYouWant = totalWinnersYouWant.current.value;

            setLoadingRandomWinners(true);
            await gameContract.methods.selectRandomWinnersFrom(_totalPlayers, _totalWinnersYouWant)
                .send({ from: account })
                .on('confirmation', (confirmationNumber, receipt) => {
                    loadRandomWinners();
                })
                .on('error', function (error, receipt) {
                    loadData();
                });
        } else {
            setMainLoading(false);
            connectToAccount();
        }
    }


    const loadRandomWinners = async () => {
        const getCurrentRoundForWinners = await gameContract.methods.currentRoundForWinners().call();
        setCurrentRoundForWinners(getCurrentRoundForWinners.toNumber());

        const _winnerPlayers = await gameContract.methods.randomSeletedWinnersInWinnersRound(getCurrentRoundForWinners.toNumber() - 1).call();
        console.log(_winnerPlayers);
        setLoadedRandomWinners(_winnerPlayers);
        setLoadingRandomWinners(false)
    }

    function timeConverter(UNIX_timestamp) {
        var a = new Date(UNIX_timestamp * 1000);
        var months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        var year = a.getFullYear();
        var month = months[a.getMonth()];
        var date = a.getDate();
        var hour = a.getHours();
        var min = a.getMinutes();
        var sec = a.getSeconds();
        var time = date + ' ' + month + ' ' + year + ' ' + hour + ':' + min + ':' + sec;
        return time;
    }


    return (
        <div className="main-box mx-auto mt-2 p-4 text-light text-center">
            <div className="bg-holder" style={{ backgroundImage: `url(${bgImage})`, backgroundPosition: 'center', backgroundSize: 'contain' }}></div>

            {/* <div className="glow-div">
                <img className="img-fluid mb-4" src={banner} style={{ borderRadius: 20 }} alt="youtube banner" />
            </div> */}
            <div onClick={connectToAccount} className="glassBg-box mx-6 py-3">
                <h1>Find Random Winners</h1>
                {connected &&
                    <Link to="/random-winner-history">
                        <button className="btn btn-hover color-1 btn-glow px-5">View History of Random Winners</button>
                    </Link>
                }
            </div >
            {!connected
                ?
                <div className="my-4">
                    <h4>Not connected. Please Connect with Web3 (Metamask, trust Wallet)</h4>

                    <button onClick={findRandomWinners} className="btn btn-hover color-1 btn-glow px-5 my-4">Connect</button>

                </div>
                :
                mainLoading
                    ?
                    <div class="spinner-border text-light my-5" role="status">
                    </div>
                    :
                    <div className="my-5 ">
                        <div className="text-center">
                            <div class="row justify-content-center">

                                <div class="col-4">
                                    <input type="number" className="py-3" ref={totalPlayers} class="form-control form-control-lg" placeholder="Total Player" />
                                </div>
                                <div class="col-4">
                                    <input type="number" ref={totalWinnersYouWant} class="form-control form-control-lg" placeholder="How many winners do you want?" />
                                </div>

                                <div className="col-12 mt-3">
                                    <button onClick={findRandomWinners} disabled={!isAdmin} className="btn btn-hover color-9 btn-glow px-5">Find Random Winners</button>
                                </div>

                                {findingRandomWinnersNotice &&
                                    <p>{findingRandomWinnersNotice}</p>
                                }

                                {loadingRandomWinners
                                    ?

                                    <div class="spinner-border text-light" role="status">
                                    </div>
                                    :
                                    loadedRandomWinners &&
                                    <div className="">
                                        <h1 className="glow-div p-3" >Random Winners : </h1>
                                        {/* <h3>{loadedRandomWinners}</h3> */}
                                        <div className="d-flex justify-content-center">
                                            {loadedRandomWinners.map((winner, key) => {
                                                return (
                                                    <h3 key={key}>{winner.toString()},</h3>
                                                )
                                            })}
                                        </div>

                                    </div>
                                }



                            </div>

                        </div>
                    </div>
            }

        </div >
    )
}



